import './App.css';
import ScanImage from './ScanImage';
import {useState,useEffect} from 'react';
import Navig from './Navig';
import {target,macys,walmart,jcpenny,costco,ashley,lowes,staples,homedepot,bestbuy} from './Urlinfo';

function App() {
  const [url,seturl] =useState(generateurl(macys.start,macys.end))  
  const [link,setlink] =useState('')
  const generateurl = (start,end) => {
    var arr = [];
    while(start < end+1){
      var url ="https://i.blackfriday.com/imagery/ad-scan-pages/"+start+".1605576978.fit_lim.size_850x.jpg"
      arr.push(url);
      start++;
      }
      return arr
  }
  const handleClick = (x) => {
     switch(x) {
      case 'walmart':
        seturl(generateurl(walmart.start,walmart.end))
        setlink(walmart.link)
        break;
      case 'macys':
        seturl(generateurl(macys.start,macys.end))
        setlink(macys.link)
        break;
      case 'target':
        seturl(generateurl(target.start,target.end))
        setlink(target.link)
        break;
      case 'jcpenny':
        seturl(generateurl(jcpenny.start,jcpenny.end))
        setlink(jcpenny.link)
        break;
      case 'costco':
        seturl(generateurl(costco.start,costco.end))
        setlink(costco.link)
        break;
            case 'ashley':
        seturl(generateurl(ashley.start,ashley.end))
        setlink(ashley.link)
        break;
            case 'lowes':
        seturl(generateurl(lowes.start,lowes.end))
        setlink(lowes.link)
        break;
            case 'staples':
        seturl(generateurl(staples.start,staples.end))
        setlink(staples.link)
        break;
        case 'homedepot':
        seturl(generateurl(homedepot.start,homedepot.end))
        setlink(homedepot.link)
        break;
        case 'bestbuy':
        seturl(generateurl(bestbuy.start,bestbuy.end))
        setlink(bestbuy.link)
        break;
        default:
    // code block
      }
  }
  useEffect (() =>{ },[url])
  return (
    <div className="App">
        <Navig handleClick={handleClick} />
        <ScanImage url={url} link={link}/>
    </div>
  );

}

export default App;